.login{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    background-color: white;
}



.login__logo{
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
}
.login__continer{
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius:5px ;
    border: 1px solid lightgreen;
    padding: 20px;

}
.login__continer>h1{
    font-weight: 500;
    margin-bottom: 20px;
}
.login__continer>form>h5{
    margin-bottom: 5px;

}
.login__continer>form>input{
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 92%;
}
.login__continer>p{
    margin-top: 15px;
    font-size: 12px;
}

.login__signinbutton{
    background:#f0c14b ;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #fa8734 #9c7e31 #846a29;
}
.login__registerbutton{
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkblue;

}